.footer-wrapper {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  justify-content: space-evenly;
  gap: 1rem;
  width: 100%;
  padding: 7rem 2rem;
  background: var(--dark);
  color: white;
  font-size: 1.2rem;
}
.footer-wrapper > * {
  max-width: 30%;
  min-width: 30%;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  flex-direction: column;
  gap: 0.8rem;
}
.footer-wrapper h3 {
  border-color: var(--light) !important;
}
.reach-us > div {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  flex-direction: column;
  gap: 0.3rem;
}
.reach-us > div > span {
  display: block;
  margin: 3px 5px;
  color: var(--primary-color);
}
.reach-us > div > span p {
  font-family: "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans",
    "Helvetica Neue", sans-serif;
  font-weight: normal;
  display: inline;
  padding-inline: 3px;
}
.reach-us--address::before {
  content: "\f3c5";
}
.reach-us--phone::before {
  content: "\f0e0";
}
.reach-us--email::before {
  content: "\f879";
}

.quick-links ul {
  list-style: none;
}
.quick-links ul li {
  display: block;
  margin-bottom: 1rem;
}
.quick-links ul li::before {
  content: "\f105";
}
.quick-links ul li a {
  color: white;
  text-decoration: none;
  font-weight: normal;
  margin-left: 0.2rem;
  font-family: "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans",
    "Helvetica Neue", sans-serif;
}
.quick-links ul li a:hover {
  color: var(--primary-color);
}
.news-letter--input-elements {
  display: flex;
  margin-block: 1rem;
}
.news-letter input {
  border: none;
  padding: 0.7rem;
  font-size: 1.1rem;
  border-radius: 20px 0px 0px 20px;
}
.news-letter button {
  border: none;
  padding: 0.7rem;
  font-size: 1.1rem;
  border-radius: 0px 20px 20px 0px;
  background: var(--primary-color);
}
