@import url("https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300;500&display=swap");
* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}
:root {
  --primary-color: #13c5dd;
  --dark: #1d2a4d;
  --light: rgb(255, 255, 255);
}
body {
  overflow-x: hidden;
}
body::-webkit-scrollbar {
  width: 15px;
  background-color: #f5f5f5;
}

body::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-image: linear-gradient(45deg, var(--primary-color), var(--dark));
}
#root > * {
  overflow: hidden;
}

/*-----Utilities-----*/
.text-primary {
  color: var(--primary-color);
}
.nav-links-wd {
  text-decoration: none;
}
.list-ws {
  list-style: none;
}
.fa {
  font-family: "Font Awesome 5 Free";
}
.nav-links-wd {
  color: black;
  font-size: 1.2rem;
  font-weight: bolder;
  color: var(--dark);
}
.button {
  display: inline-block;
  font-size: 1.1rem;
  font-family: "Roboto", sans-serif;
  width: 10rem;
  padding-block: 15px;
  border-radius: 30px;
  font-weight: 500;
  cursor: pointer;
  border: 0px;
}
.primary-heading {
  font-size: 2.6rem;
  font-family: "Roboto", sans-serif;
}
.secondary-heading {
  border-bottom: 4px solid var(--primary-color);
  display: inline-block;
  width: fit-content;
}

.text-dark {
  color: var(--dark);
}
.bold {
  font-weight: bold;
}
.text-light {
  color: var(--light);
}
/*-----Navigation----*/
nav {
  position: fixed;
  top: 1rem;
  left: 0px;
  right: 0px;
  width: 1000px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: white;
  height: 4rem;
  padding-inline: 1rem;
  z-index: 1000;
  border-radius: 3rem;
  margin: 0px auto;
  box-shadow: 0px 1px 10px #1d2a4dde;
}
.logo-sect {
  display: flex;
  font-size: 1.6rem;
  gap: 5px;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: center;
}
.logo-sect h1 {
  font-family: "Bebas Neue", cursive;
}
.logo-sect span::before {
  content: "\f7f2";
}
.nav-list > ul li {
  display: inline-block;
  margin-inline: 0.5rem;
}
.nav-list > ul li a {
  padding: 5px 10px;
  border-radius: 3rem;
}
.nav-list > ul li a:hover {
  background: var(--primary-color);
  color: var(--light);
}
.dropdown-toggle::after {
  display: inline-block;
  margin-left: 0.255em;
  vertical-align: 0.255em;
  content: "";
  border-top: 0.3em solid;
  border-right: 0.3em solid transparent;
  border-bottom: 0;
  border-left: 0.3em solid transparent;
}
.dropdown-toggle:hover {
  cursor: pointer;
}
.nav-dropdown {
  display: none;
  position: absolute;
  top: 6rem;
  box-shadow: 0px 1px 10px rgba(134, 134, 134, 0.604);
  right: 5%;
  padding-block: 5px;
  background-color: white;
  border-radius: 5px;
  z-index: 1000;
}

.nav-dropdown li {
  padding: 5px 30px;
}
.nav-dropdown li:hover {
  background-color: var(--primary-color);
}

/*-----hero-section----*/
.hero-wrapper {
  font-family: "Roboto", sans-serif;
  position: relative;
  width: 100%;
  min-height: 70vh;
}

.hero-wrapper::before {
  content: "";
  position: absolute;
  top: 0px;
  left: 0px;
  height: 100%;
  width: 100%;
  background: url("./media/woman-g63242e222_1920.jpg");
  background-size: cover;
  z-index: -200;
}
.hero-wrapper::after {
  content: "";
  position: absolute;
  top: 0px;
  left: 0px;
  height: 100%;
  width: 100%;
  background: linear-gradient(45deg, var(--dark), var(--dark), var(--light));
  background-size: cover;
  z-index: -199;
  opacity: 0.45;
}
.hero {
  display: flex;
  position: absolute;
  flex-direction: column;
  height: 100%;
  width: 100%;
  align-items: center;
  justify-content: center;
}
.hero-buttons {
  position: absolute;
  bottom: 2rem;
}

.hero h1 {
  color: var(--light);
}
.find-doc-button {
  background-color: var(--primary-color);

  color: var(--light);
  transition: all 0.1s ease;
  margin-inline: 5px;
}
.find-doc-button:hover {
  background-color: var(--light);
  color: var(--dark);
}

.appointment-button {
  color: var(--dark);
  margin-inline: 5px;
  border: 1px solid white;
  box-sizing: border-box;
}
.appointment-button:hover {
  background-color: transparent;
  color: var(--light);
}

/*-----about-section-----*/
.about-wrapper {
  position: relative;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 5rem 2rem;
}
.about-wrapper::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0%;
  width: 100px;
  height: 100px;
  background: var(--dark);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  z-index: -10;
  opacity: 0.3;
  transform: rotate(45deg);
}
.about-wrapper::after {
  content: "";
  position: absolute;
  bottom: 0;
  right: 0%;
  width: 100px;
  height: 100px;
  background: var(--primary-color);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  z-index: -10;
  opacity: 0.3;
  transform: rotate(45deg);
}
.about {
  display: grid;
  grid-template-columns: 1fr 1.5fr;
  max-width: 1300px;
  gap: 20px;
}
.img-wrapper {
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  border-radius: 20px;
}
.about img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}
.about-content {
  display: flex;
  gap: 20px;
  flex-direction: column;
}
.about-cards {
  display: flex;
  align-items: center;
  width: 100%;
  justify-content: center;
  gap: 20px;
  border-top: 4px solid var(--primary-color);
}
.round-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-radius: 0rem 0rem 6rem 6rem;
  padding: 20px 30px;
  background-color: var(--primary-color);
}
.round-card img {
  width: 80px;
}
@media screen and (max-width: 920px) {
  .about {
    display: flex;
    flex-direction: column;
  }
}

/*-----services-section-----*/
.services-wrapper {
  display: flex;
  position: relative;
  justify-content: center;
  align-items: center;
  width: 100%;
  text-align: center;
  padding: 5rem 2rem;
}
.services::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 0%;
  width: 200px;
  height: 100px;
  background: var(--dark);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  z-index: -10;
  opacity: 0.3;
  transform: rotate(35deg);
}
.services::after {
  content: "";
  position: absolute;
  bottom: 50%;
  right: 0%;
  width: 100px;
  height: 300px;
  background: var(--primary-color);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  z-index: -10;
  opacity: 0.3;
  transform: rotate(35deg);
}
.services {
  width: 100%;
  max-width: 1300px;
  margin: 0px auto;
  padding: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 1rem;
}
.service-cards {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  gap: 2rem;
}
.service-card {
  width: 20rem;
  padding: 2rem;
  aspect-ratio: 10/10;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  gap: 10px;
  align-items: center;
  justify-content: center;
  background-color: rgba(207, 247, 255, 0.527);
  transition: all 0.3s;
}
.service-card:hover {
  box-shadow: 0px 1px 10px rgba(60, 138, 136, 0.478);
  cursor: pointer;
}
.service-cover {
  background: var(--primary-color);
  padding: 2rem;
  overflow: hidden;
  border-radius: 50%;
}
.service-cover img {
  width: 90px;
  aspect-ratio: 1/1;
}
/*-----services-section-----*/

.appointment-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  background: var(--primary-color);
  padding: 10rem 2rem;
  margin-block: 10rem;
}
.heading-light {
  color: white !important;
  border-bottom: 4px solid white !important;
}
.appointment {
  display: grid;
  grid-template-columns: 1fr 1fr;
  max-width: 1350px;
  column-gap: 3rem;
}
.appoint-desc {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
.appointment-wrapper form {
  background: white;
  flex-direction: column;
  display: flex;
  padding: 3rem 2rem;
  align-items: center;
  justify-content: space-between;
  border-radius: 20px;
  gap: 1.3rem;
}
.appointment-wrapper .form-row {
  display: flex;
  flex-wrap: no-wrap;
  column-gap: 1.3rem;
  align-items: center;
  justify-content: center;
  width: 100%;
}
.appointment-wrapper .form-child {
  width: 45%;
  border-radius: 10px;
  border: 2px solid #6f6f6fa6;
  padding: 15px 5px;
}
.form-child:focus {
  border: none;
}
.form-button {
  background: var(--primary-color);
  width: 100% !important;
  transition: all 0.3s;
}
.form-button:hover {
  box-shadow: 0px 1px 7px #1d2a4d96;
}
.find-doc-dark {
  background-color: var(--dark);
  color: var(--primary-color);
  margin-right: 10px;
  transition: all 300ms;
}
.find-doc-dark:hover {
  box-shadow: 0px 1px 5px var(--dark);
  background-color: white;
  color: var(--dark);
}
.read {
  color: var(--dark);
  transition: all 300ms;
}
.read:hover {
  background: var(--dark);
  color: white;
}
@media screen and (max-width: 840px) {
  .appointment {
    display: flex;
    flex-direction: column;
    gap: 2rem;
  }
}

/*----Mp-section----*/
.mp-section-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 5rem 2rem;
  width: 100%;
}
.mp-section {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
}
.mp-section::before {
  content: "";
  position: absolute;
  top: 0;
  left: -1%;
  width: 100px;
  height: 100px;
  background: var(--dark);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  z-index: -10;
  opacity: 0.3;
  transform: rotate(55deg);
}
.mp-section::after {
  content: "";
  position: absolute;
  bottom: 0;
  right: -1%;
  width: 100px;
  height: 100px;
  background: var(--primary-color);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  z-index: -10;
  opacity: 0.3;
  transform: rotate(55deg);
}
.mp-cards-scroller {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  border-radius: 30px;
}
.mp-cards-wrapper {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: center;
  padding: 1rem;
  width: 100%;
  gap: 1rem;
}

.picture-card {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 20rem;
  align-items: center;
  justify-content: flex-start;
  border-radius: 10px;
  overflow: hidden;
  max-height: 455px;
  box-shadow: 0px 1px 7px rgba(175, 175, 175, 0.62);
}

.pic-card-upper-sec {
  position: relative;
  height: 50%;
  display: flex;
  padding: 5rem 1rem;
  align-items: center;

  justify-content: center;
  width: 100%;
}
.pic-card-upper-sec-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
}
.pic-card-upper-sec-content span h3 {
  display: inline;
}
.pic-card-upper-sec::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    45deg,
    var(--dark),
    var(--dark),
    var(--primary-color)
  );
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  z-index: -1;
  opacity: 0.7;
}
.pic-card-upper-sec-before {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  z-index: -2;
  object-fit: cover;
}
.pic-card-lower-sec {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1rem;
  text-align: center;
  gap: 0.5rem;
  color: var(--dark);
  background: rgba(207, 247, 255, 0.527);
  justify-content: center;
}
.pic-card-lower-sec button {
  background: var(--primary-color);
  font-weight: lighter;
  transition: all 100ms ease;
}
.pic-card-lower-sec button:hover {
  box-shadow: 0px 1px 5px var(--dark);
  background-color: var(--dark);
}

/*----doctors-section*/

.doctors-section-wrapper {
  padding: 5rem 2rem;
  position: relative;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 1rem;
  overflow: hidden;
}
.doctors-section-wrapper::before {
  content: "";
  position: absolute;
  bottom: 0%;
  left: 10%;
  width: 10rem;
  height: 30rem;
  background: var(--primary-color);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  transform: rotate(45deg);
  z-index: -1;
  opacity: 0.5;
}
.doctors-section-wrapper::after {
  content: "";
  position: absolute;
  bottom: 0%;
  right: 10%;
  width: 10rem;
  height: 30rem;
  background: var(--dark);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  transform: rotate(45deg);
  z-index: -1;
  opacity: 0.5;
}
.doctor-cards-scroller {
  display: flex;
  align-items: flex-start;
  width: 100%;
  max-width: 1350px;
  flex-shrink: 0;
  flex-wrap: nowrap;
  overflow-x: scroll;
  scroll-behavior: smooth;
  padding-block: 1rem;
  gap: 1rem;
  scroll-snap-type: inline mandatory;
}

.doctor-cards-scroller::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: #ffffff;
  border-radius: 10px;
  margin-inline: 10rem;
}

.doctor-cards-scroller::-webkit-scrollbar {
  height: 10px;
  background-color: #ffffff00;
}

.doctor-cards-scroller::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-image: linear-gradient(45deg, var(--primary-color), var(--dark));
}

.doctor-card {
  display: flex;
  gap: 1rem;
  scroll-snap-align: start;
  min-width: 35rem;
  background: #e7e7e7;
  border-radius: 10px;
  overflow: hidden;
  height: 16rem;
}

.doctor-card-img-wrapper > img {
  height: 100%;
  width: 100%;
  background-position: center;
  background-size: cover;
  object-fit: cover;
}
.doctor-details {
  width: 18rem;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  gap: 7px;
}
.doctor-details h1 {
  font-size: 1.7rem;
}
.social-links {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: center;
  gap: 0.3rem;
  border-top: 1px solid var(--dark);
  padding-top: 0.3rem;
}
.social-link img {
  width: 35px;
}
.social-link {
  background: var(--dark);
  border: none;
  border-radius: 50%;
  padding: 0.4rem;
  width: calc(35px + 1rem) !important;
  transition: all 0.1s ease;
}
.social-link:hover {
  transform: translateY(-1px);
  box-shadow: 0px 1px 7px #1d2a4d96;
}
/*---Find A doctor---*/
.find-a-doctor-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 10rem 2rem;
  margin-block: 10rem;
  gap: 1rem;
  position: relative;
  background: var(--primary-color);
}
.find-a-doctor-wrapper > * {
  z-index: 3;
}
.find-a-doctor-wrapper::before {
  content: "";
  position: absolute;
  bottom: -10%;
  right: 37%;
  width: 30rem;
  height: 30rem;
  background: rgba(255, 255, 255, 0.665);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  transform: rotate(45deg);
  z-index: 2;
  opacity: 0.5;
}
.find-a-doctor-wrapper::after {
  transform: rotate(45deg);
  content: "";
  position: absolute;
  bottom: 47%;
  left: -26%;
  width: 100%;
  height: 1rem;
  background: rgba(255, 255, 255, 0.665);
  z-index: 2;
  opacity: 0.5;
}
.find-a-doctor-wrapper h3 {
  color: white;
  border-bottom: 4px solid var(--light);
}
.find-a-doctor-wrapper p {
  font-size: 1.2rem;
  width: 50%;
  text-align: center;
}
.find-a-doctor-wrapper form {
  display: flex;
  margin-top: 1rem;
  border-radius: 30px;
  overflow: hidden;
  border: 1px solid var(--primary-color);
}
.find-a-doctor-wrapper form > * {
  border: none;
  padding: 1rem;
}
.find-a-doctor-wrapper form > input {
  border-inline: 1px solid var(--primary-color);
  font-size: 1.1rem;
}
.find-a-doctor-wrapper form select {
  border-radius: 30px 0px 0px 30px;
}
.search-button {
  border-radius: 0px 30px 30px 0px !important;
  background: var(--dark);
}

/*--testimonial -section ---*/
.testimonial-section-wrapper {
  padding: 5rem 2rem;
  margin-bottom: 5rem;
  position: relative;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 1rem;
}
.testimonial-card-carousel {
  margin: 1rem;
  width: 100%;
  max-width: 1350px;
  position: relative;
}
.carousel-container {
  display: flex;
  width: 100%;
}
.testimonial-card-carousel-track {
  display: block;
  width: 100%;
}
.testimonial-card-carousel-track ul {
  width: 100%;
  position: relative;
  list-style: none;
  height: 30rem;
}
.testimonial-card-carousel-track ul li {
  display: block;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  transition: opacity 0.3 ease;
}
.testimonial-card-carousel-track ul li.active {
  opacity: 1;
}
.testimonial-card {
  min-width: 100%;
  display: flex;
  flex-direction: column;
  gap: 1rem;
  align-items: center;
  min-height: 30rem;
  flex-grow: 1;
  flex-basis: 1;
  max-height: 30rem;

  justify-content: center;
}
.testimonial-carousel-nav {
  padding: 1.5rem;
  position: absolute;
  top: 50%;
  border: none;
  transform: translateY(-50%);
  font-size: 1.3rem;
  border-radius: 50%;
  background: var(--primary-color);
}
.testimonial-carousel-nav--left {
  left: 0px;
}
.testimonial-carousel-nav--right {
  right: 0px;
}
/*inner card styling*/
.testimonial-card p {
  max-width: 40rem;
  font-size: 1.2rem;
  font-family: "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans",
    "Helvetica Neue", sans-serif;
  text-align: center;
}
.testimonial-img-wrapper {
  min-width: 200px;
  min-height: 200px;
  max-width: 200px;
  max-height: 200px;
  border-radius: 50%;
  overflow: hidden;
  border: 1px solid var(--primary-color);
}
.testimonial-img-wrapper img {
  width: 100%;
  height: 100%;
  background-position: center;
  background-size: cover;
  object-fit: cover;
}

/*---footer ----*/
